import React, { Component } from 'react';
import './App.css';

class Book extends Component {
  render() {
    const {
      title,
      link,
      image_url
    } = this.props.book;

    return (
      <a href={link} title={title}>
        <img className="book" src={image_url} alt={title} />
      </a>
    );
  }
}

class App extends Component {
  constructor() {
    super();
    this.state = {
      shelf: false
    };
  }
  
  componentDidMount() {
    fetch('https://goodreads-shelves.herokuapp.com/')
      .then((response) => response.json())
      .then(response => {
        this.setState({
          shelf: response
        });
      })
      .catch((error) => {
        console.error(error);
      });
  }

  renderCurrentShelf() {
    const { books } = this.state.shelf.current;

    return (
      <div id="current-shelf">
        {books.map((book, id) => (
          <Book book={book.book} key={id} />
        ))}
      </div>
    );
  }

  renderReadShelf() {
    const { books } = this.state.shelf.read;

    return (
      <div id="read-shelf">
        {books.map((book, id) => (
          <Book book={book.book} key={id} />
        ))}
      </div>
    );
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h6>Dunnkers' Digital Reading Shelf <span role="img" aria-label="Book">ðŸ“™</span></h6>
        </header>
        <section className="Shelf">
          <h4 className="header">Currently reading</h4>
          {this.state.shelf ? this.renderCurrentShelf() : <span></span>}
          <h4 className="header">Read</h4>
          {this.state.shelf ? this.renderReadShelf() : <span></span>}
        </section>
      </div>
    );
  }
}

export default App;
